.footer {
  position: relative;

  &-container {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 1.5rem;
    border-top: 1px solid #202020;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
  }

  &-create-by,
  &-source-code {
    font-size: 1.3rem;
  }

  &-source-code {
    a {
      text-decoration: none;
      color: #fff;
      margin-left: 1rem;

      &:hover {
        opacity: 0.75;
      }
    }
  }
}
